import './CreateTodoButton.css';
import React from 'react';
import PropTypes from 'prop-types';

function CreateTodoButton({ setOpenModal }) {
  // Función manejadora del clic
  const handleClick = () => {
    setOpenModal(prevState => !prevState); // Alterna el estado del modal
  };

  return (
    <button
      className="CreateTodoButton"
      onClick={handleClick}
      aria-label="Abrir o cerrar el modal para crear un TODO"
    >
      +
    </button>
  );
}

// Validación de propiedades
CreateTodoButton.propTypes = {
  setOpenModal: PropTypes.func.isRequired,
};

// Propiedad predeterminada
CreateTodoButton.defaultProps = {
  setOpenModal: () => {},
};

export { CreateTodoButton };
