import React from 'react';
import ReactDOM from 'react-dom';
import './Modal.css';

/**
 * Componente Modal
 * Este componente utiliza React Portal para renderizar contenido dentro de un elemento
 * fuera del árbol principal del DOM. Ideal para modales, popups y otros elementos flotantes.
 *
 * @param {React.ReactNode} children - Contenido que se mostrará dentro del modal.
 * @returns {JSX.Element} El modal renderizado en el portal designado.
 */
function Modal({ children }) {
  // Verifica si el elemento 'modal' existe en el DOM antes de intentar renderizar
  const modalRoot = document.getElementById('modal');
  if (!modalRoot) {
    console.error('No se encontró un elemento con id "modal" en el DOM.');
    return null;
  }

  return ReactDOM.createPortal(
    <div className="ModalBackground">
      {children}
    </div>,
    modalRoot
  );
}

export { Modal };
